@import url('https://fonts.googleapis.com/css2?family=Comfortaa&display=swap');

.navbar {
  position: absolute;
  left: 20px;
  top: 20px;
  /* border: 1px solid black; */
  width: 150px;
  height: 745px;
  padding: 5px;
  background-color: #c6b198;
  border-radius: 25px;
  z-index: 100;
  text-align: center;
}
.displayimage {
  position: relative;
  left: 10px;
  background-image: url("../assets/DivyeGupta.jpeg");
  width: 100px;
  height: 100px;
  background-size: contain;
  background-position: center;
  border-radius: 50%;
  margin-left: 15px;
  margin-top: 15px;
}

.name{
  font-family: 'Comfortaa', cursive;
